.infoBox{
    font-family: helvetica;
    margin: 1em;
    background: #f45a6a;
    border-Radius: 8px 8px 8px 8px;
    color: #e0e0e0;
    padding: 0.5em;
    transition: 0.9s ease-out;
  }


  .title{
    font-family: helvetica;
    padding-top: 0em;
    font-size: 4em;
    text-align: left;
    margin-bottom: 0em;
    color: #e0e0e0;
    transition: 0.2s ease;
  }

  .info{
    font-family: helvetica;
    text-align: left;
    font-size: 2em;
    color: #e0e0e0;
    margin: 0em;
    transition: 0.3s ease-out;
  }

  .news {
    font-family: helvetica;
    margin: 1em;
    border-Radius: 8px 8px 8px 8px;
    color: #e0e0e0;
    transition: 0.9s ease-out;
    text-align: left;
    transition: 0.4s ease-out;
  }
@media only screen and (min-width: 1000px) {
.infoBox{
  width: 65%;
  margin-left: auto;
  margin-right: auto;
}
}  

